* {
  margin: 0;
  box-sizing: border-box;
}

canvas {
   width: 100vw;
   height: 100vh
}


body {
  margin: 0px;
  overflow: hidden;
}



* { -webkit-tap-highlight-color:rgba(0,0,0,0);
margin: 0;
box-sizing: border-box;
font-family: 'Archivo', sans-serif;
}

.Preloader-container {
   opacity: 1;
   position: fixed;
   left: 0;
   top: 0;
   z-index: 1000;
   width: 100%;
   height: 100%;
   background: linear-gradient(180deg, #FFB800 0%, #FFC736 100%);
   display: flex;
   align-items: center;
   justify-content: center;
   flex-direction: column;
   gap: 25px;
   transition: all 0.4s ease;
}

.Preloader-container img {
   width: 120px;
   height: auto;
   object-fit: contain;
  
}

.progress-bar {
   width: 300px;
   height: 6px;
   border-radius: 30px;
   background: rgb(255, 210, 128);
}

.progress {
   width: 0%;
   height: inherit;
   background: white;
   border-radius: inherit;
   transition: all 0.15s ease
}

.main-container {
   position: relative;
   width: 100vw ;
   height: 100vh ;
   display: grid;
grid-template-columns: 200px 1fr;
grid-template-rows: 1fr;
grid-column-gap: 35px;
grid-row-gap: 0px;
padding: 30px 23px;
}

.menu {
   position: relative;
   width: 100%;
   height: 100%;
   background: #FFE100;
   border-radius: 20px;
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 25px;
   padding: 15px 8px;
}

.Menu-text {
   padding-bottom: 10px;
   width: calc(100% - 15%);
   text-align: center;
   border-bottom: 3px solid black;
}

.main-container canvas {
   position: relative;
  width: 100%;
  height: 100%;
  border: 0;
  border-radius: 15px;
  box-shadow: 0 3px 18px rgba(0,0,0,0.14)
}

.paper-btn {
  opacity: 0.4;
  width: calc(100% - 11%);
  height: 55px;
  font-size: 18px;
  background: linear-gradient(180deg, #3C72FF 0%, #0047FF 100%);
  border: 4px solid #5E8BFF;
  color: white;
  border-radius: 150px;
  cursor: pointer;
  box-shadow: 0px 2px 16px rgba(80, 128, 255, 0.6);
  transition: 0.4s ease;

}

