.blur-window {
   width: 100%;
   height: 100%;
   position: absolute;
   left: 0;
   top: 0;
   
   background: linear-gradient(154.43deg, rgba(255, 255, 255, 0.08) 16.18%, rgba(255, 255, 255, 0.03) 96.13%);
   backdrop-filter: blur(15px);
   display: none;
   border-radius: 20px;
   z-index: 100;
}

.popup-custom {
   
   position: absolute;
   left: 50%;
   top: 50%;
   transform: translate(-50%,-50%);
   z-index: 10000000000000000000000000;
   padding: 15px 18px;
   max-width: 320px;
   border-radius: 22px;
   background: white;
   display: flex;
   align-items: center;
   justify-content: center;
   flex-direction: column;
   gap: 12px;
   display: none;
}

.popup-custom h1 {
  color: black;
  font-size: 25px;
  padding: 10px 48px;
  border-radius: 50px;
  background: #FFE00B;
  letter-spacing: 0.6px;
}

.popup-custom .text-1 {
   color: black;
   font-size: 17px;
   font-weight: 600 !important;
   background: #FFE00B;
   padding: 15px 15px;
   border-radius: 15px;
   letter-spacing: 0.7px;
   line-height: 1.25;
}

.popup-custom .text-2 {
  color: white;
  letter-spacing: 0.6px;
  font-weight: 100;
  font-size: 17px;

}

.popup-custom a {
   text-decoration: none;
   height: 50px;
   width: calc(100% - 5%);
}

.popup-custom button {
 width: 100%;
 height: 100%;
color: black;
background: #FFE00B;
border: 0;
