.popup-close-btn  {
   position: absolute;
   right: -38px;
   top: -38px;
   z-index: 100000000000;
   cursor: pointer;
   width: 45px;
   height: 45px;
   font-size: 32px;
   border-radius: 50%;
   display: grid;
   place-items: center;
   background: #FFE00B;
   color: black;
}

.anno {
  width:130px;


  cursor: pointer;
  transition: 0.3s ease;
}

.anno:hover {
