}

.reset-camera-btn {
   opacity: 0;
   pointer-events: none;
   width: 60px;
   height: 60px;
   border-radius: 50%;
   background: #FFE100;
   box-shadow: inset 0 2.5px 3px #fff187,
   inset 0 -2px 3px #837401;
   position: absolute;
   left: 50%;
   bottom: 40px;
   transform: translate(-50%,-50%);
   z-index: 1000;
   display: grid;
   place-items: center;
   cursor: pointer;
   transition: 0.45s ease;
}

.reset-camera-btn i {
   font-size: 35px;
   color: white;
  }

.camera-btn-active {
     opacity: 1;
     pointer-events: all;
}

